html{font-size:20px}*{box-sizing:border-box;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-touch-callout:none}body{position:absolute;top:0;right:0;bottom:0;left:0;overflow:hidden;font-family:"Helvetica Neue",Helvetica,sans-serif;font-size:.85rem;line-height:1.5;color:#3d4145}label>*{pointer-events:none}@media only screen and (min-width:400px){html{font-size:21.33333333px !important}}@media only screen and (min-width:414px){html{font-size:22.08px !important}}@media only screen and (min-width:480px){html{font-size:25.6px !important}}a{text-decoration:none;color:#61749b}.pull-right{float:right}.pull-left{float:left}.badge{display:inline-block;padding:.1rem .45rem .15rem;font-size:.6rem;line-height:1;color:#3d4145;background-color:rgba(0,0,0,0.15);border-radius:5rem}@-webkit-keyframes preloader-spin{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes preloader-spin{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.page,.view{box-sizing:border-box;position:absolute;left:0;top:0;bottom:0;right:0;width:100%;height:100%}.fade-transition{opacity:1;-webkit-transition:opacity .3s;transition:opacity .3s}.fade-enter,.fade-leave{opacity:0}.slide-transition{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);-webkit-transition:all .3s;transition:all .3s}.slide-enter{opacity:0;-webkit-transform:translate3d(70%, 0, 0);transform:translate3d(70%, 0, 0)}.slide-leave{opacity:0;-webkit-transform:translate3d(-70%, 0, 0);transform:translate3d(-70%, 0, 0)}.transition-reverse .slide-enter{opacity:0;-webkit-transform:translate3d(-70%, 0, 0);transform:translate3d(-70%, 0, 0)}.transition-reverse .slide-leave{opacity:0;-webkit-transform:translate3d(70%, 0, 0);transform:translate3d(70%, 0, 0)}.page-content{position:absolute;top:0;right:0;bottom:0;left:0;overflow:auto;-webkit-overflow-scrolling:touch}.content-padded{margin:.5rem}


